import React from "react"
import { connect } from 'react-redux'
import { NotificationManager } from "react-notifications";

import { Input, Layout, Button, Form } from "antd";
import IntlMessages from "../../../../util/IntlMessages";

import { inviteReaders } from '../../../../appRedux/actions/projects'
class InviteReaders extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            author1_email: '',
            author2_email: '',
            author3_email: '',
            hideBtn: true
        };
        this.onChange = this.handleChange.bind(this)
        this.onSubmit = this.handleSubmit.bind(this);
    }
    handleChange = event => {
        this.setState({
            [event.target.name]: event.target.value,
            hideBtn: false
        })
    }

    handleSubmit = event => {
        event.preventDefault();
        const { author1_email, author2_email, author3_email } = this.state;
        this.props.inviteReaders(this.props.project.slug, { author1_email, author2_email, author3_email })
            .then(() => {
                NotificationManager.success(<IntlMessages id="notification.projectInviteAuthors" />, <IntlMessages id="notification.success" />)
                this.setState({ hideBtn: true })
            })

    }
    render() {
        return (
            <Layout>
                <Form onSubmit={this.handleSubmit} >
                    <h3><IntlMessages id="miuwi.project.diffusion.inviteTitle" /></h3>
                    <p><IntlMessages id="miuwi.project.diffusion.inviteText" /></p>
                    <div style={{ padding: "15px", paddingBottom: "0" }}>
                        <Form.Item >
                            <label style={{ float: "left" }}> <IntlMessages id="miuwi.project.diffusion.lecteurLabel" /> 1&nbsp;&nbsp;</label>
                            <Input
                                name="author1_email"
                                style={{ width: 200 }}
                                value={this.state.author1_email ? this.state.author1_email : ( this.props.project ) ? this.props.project.author1_email : "" }
                                onChange={this.handleChange}
                                placeholder="Email"
                            />
                        </Form.Item>
                        <Form.Item>
                            <label style={{ float: "left" }}><IntlMessages id="miuwi.project.diffusion.lecteurLabel" /> 2&nbsp;</label>
                            &nbsp;
                            <Input
                                name="author2_email"
                                style={{ width: 200 }}
                                value={this.state.author2_email ? this.state.author2_email : ( this.props.project ) ? this.props.project.author2_email : ""}
                                onChange={this.handleChange}
                                placeholder="Email"
                            />
                        </Form.Item>
                        <Form.Item>
                            <label style={{ float: "left" }}><IntlMessages id="miuwi.project.diffusion.lecteurLabel" /> 3&nbsp;</label>
                            &nbsp;
                            <Input
                                name="author3_email"
                                style={{ width: 200 }}
                                value={this.state.author3_email ? this.state.author3_email : ( this.props.project ) ? this.props.project.author3_email : ""}
                                onChange={this.handleChange}
                                placeholder="Email"
                            />
                        </Form.Item>
                    </div>
                    <div >
                        <Button htmlType="submit" type="primary" >
                            <IntlMessages id="miuwi.project.diffusion.inviteSubmitBtn" />
                        </Button>

                    </div>
                </Form>
            </Layout>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        state,
        project: state.projects.project
    }
}

export default connect(mapStateToProps, { inviteReaders })(InviteReaders)